  height: 80px;
  display: flex;

  align-items: center;
  font-size: 1.2rem;
  z-index: 1;
  color: white;
}
/* ul table tr td{
  width: 100px;
} */
td {
  width: 220px;
}
.navbar-logo {
  color: black;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

